.section {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  row-gap: 30px;

  @media (max-width: 700px) {
    margin-top: 20px;
  }

  &_title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid silver;
    padding-bottom: 10px;

    @media (max-width: 700px) {
      flex-wrap: wrap;
      row-gap: 20px;
    }

    h2 {
      font-size: 28px;
      font-weight: 600;

      @media (max-width: 700px) {
        font-size: 22px;
      }

    }

    button {
      background-color: #005382;
      border: none;
      color: #FFFFFF;
      cursor: pointer;
      font-size: 17px;
      font-weight: 600;
      padding: 14px;
      border-radius: 5px;
      transition: 0.3s;

      @media (max-width: 700px) {
        font-size: 15px;
        padding: 11px;
      }

      &:hover {
        background-color: #003F63;
      }

    }

  }

  &_movies {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    column-gap: 20px;
    row-gap: 50px;

    @media (max-width: 900px) {
      justify-content: center;
    }

    @media (max-width: 450px) {
      gap: 12px;
    }

  }

  &_bottomButton {
    display: flex;
    justify-content: center;
  }

}