.container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
}

.slider {
  position: relative;
  width: 250px;

  @media (max-width: 950px) {
    width: 500px;
  }

  @media (max-width: 540px) {
    width: 400px;
  }

  @media (max-width: 440px) {
    width: 330px;
  }

}

.slider_track,
.slider_range,
.slider_left_value,
.slider_right_value {
  position: absolute;
}

.slider_track,
.slider_range {
  border-radius: 3px;
  height: 5px;
}

.slider_track {
  background-color: #ced4da;
  width: 100%;
  z-index: 1;
}

.slider_range {
  background-color: #003F63;
  z-index: 2;
}

.thumb, .thumb::-webkit-slider-thumb {
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
}

.thumb {
  pointer-events: none;
  position: absolute;
  height: 0;
  width: 250px;
  outline: none;

  @media (max-width: 950px) {
    width: 500px;
  }

  @media (max-width: 540px) {
    width: 400px;
  }

  @media (max-width: 440px) {
    width: 330px;
  }

}

.thumb_left {
  z-index: 3;
}

.thumb_right {
  z-index: 4;
}

.thumb::-webkit-slider-thumb {
  background-color: #f1f5f7;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 1px 1px #ced4da;
  cursor: pointer;
  height: 18px;
  width: 18px;
  margin-top: 4px;
  pointer-events: all;
  position: relative;
}

.thumb::-moz-range-thumb {
  background-color: #f1f5f7;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 1px 1px #ced4da;
  cursor: pointer;
  height: 18px;
  width: 18px;
  margin-top: 4px;
  pointer-events: all;
  position: relative;
}

